.view-id-carousels.view-display-id-block_1 {
  .hide-overflow &{
    overflow-y: hidden;
  }
  .slick-arrow {
    z-index: 10;

    &.slick-prev {
      left: 15px;
    }
    &.slick-next {
      right: 15px;
    }
  }
  .slick-list {
    margin: 0 -10px;
    .slick-slide {
      float: left;
      padding: 0 10px;
      opacity: 0.6;
      transition: ease all 0.4s;
      &.slick-active {
        opacity: 1;
      }
      img {
        width: 100%;
        height: auto;
        max-width: 100%;
      }
    }
  }
}
